@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-style: $style;
    font-weight: #{$weight};
    font-family: "#{$font-family}";

    font-display: swap;
    src: url("../fonts/#{$url}.woff2") format("woff2"),
    url("../fonts/#{$url}.woff") format("woff");
  }
}
@include font-face("Poppins", "Poppins-Light", 300, normal);
@include font-face("Poppins", "Poppins-Regular", 400, normal);
@include font-face("Poppins", "Poppins-SemiBold", 600, normal);
@include font-face("Poppins", "Poppins-Bold", 700, normal);

@include font-face("Montserrat", "Montserrat-Regular", 400, normal);
@include font-face("Montserrat", "Montserrat-SemiBold", 600, normal);
@include font-face("Montserrat", "Montserrat-Bold", 700, normal);



