// --------------- hex to rgba mixin ---------------
@function HexToRGB($hex) {
  @return red($hex), green($hex), blue($hex);
}

:root {
// --------------- default global variable ---------------
  --error-bg: #d01f1f;
  --body-bg: #1f1f1f;
  --body-font-size: 1rem;

  --content-width: 95%;
  --container-offset: 0;

  --container-width: calc(var(--content-width));

// --------------- default JS variable ---------------
 --header-height: 142px;
 --team-slider-offset: clamp(15px, 3.90625vw, 40px);



 @include for-laptop {
   --content-width: 86%;
   --body-font-size: 1.1111vw;
 }

 @include for-desktop {
  --content-width: 85% ;
  --body-font-size: .8vw;
 }
  //
  //@include laptop {
  //  --body-font-size: 1rem;
  //}

 @include mobile {
  --content-width: calc(100% - 30px);
 }


// --------------- default scroll variable ---------------
  --scrollbar-thumb: #00A9EF;
  // --scrollbar-thumb-hover: #00aff0;
  --scrollbar-track:  #00A9EF;
  --scrollbar-track-hover: #00A9EF;
  --overlay-bg: rgba(0, 0, 0, 0.5);

// --------------- default slect variable ---------------
  --select-inner-bg: #00A9EF;
  --select-inner-border: #00A9EF;
  --select-focus-border: #00A9EF;

// --------------- default font-family variable ---------------
  --font-family: 'Popins', system-ui, sans-serif; // var(--font-family);
  --font-family2: 'Montserrat', sans-serif; // var(--font-family2);

// --------------- default text color variable ---------------
  // --burger-color: #{HexToRGB(#00d655)};

  --cl-a: #{HexToRGB(#ffffff)}; // rgba(var(--cl-a),1.0);
