  --cl-e: #{HexToRGB(#1B1B1B)}; // rgba(var(--cl-e),1.0);
  --cl-f: #{HexToRGB(#00A9EF)}; // rgba(var(--cl-f),1.0);
  --cl-g: #{HexToRGB(#F2F3F5)}; // rgba(var(--cl-g),1.0);
  --cl-h: #{HexToRGB(#232729)}; // rgba(var(--cl-h),1.0);
  --cl-i: #{HexToRGB(#2A353B)}; // rgba(var(--cl-i),1.0);
  --cl-j: #{HexToRGB(#F5F5F5)}; // rgba(var(--cl-j),1.0);
  --cl-k: #{HexToRGB(#1F1F1F)}; // rgba(var(--cl-k),1.0);
  --cl-l: #{HexToRGB(#111111)}; // rgba(var(--cl-l),1.0);



// --------------- default background color variable ---------------
  --bg-a: #{HexToRGB(#ffffff)}; // rgba(var(--bg-a),1.0);
  --bg-b: #{HexToRGB(#000000)}; // rgba(var(--bg-b),1.0);
  --bg-c: #{HexToRGB(#F9FAFE)}; // rgba(var(--bg-c),1.0);
  --bg-d: #{HexToRGB(#0f0f0f)}; // rgba(var(--bg-d),1.0);
  --bg-e: #{HexToRGB(#1B1B1B)}; // rgba(var(--bg-e),1.0);
  --bg-f: #{HexToRGB(#00A9EF)}; // rgba(var(--bg-f),1.0);
  --bg-g: #{HexToRGB(#F2F3F5)}; // rgba(var(--bg-g),1.0);
